import React, {useState, useEffect} from "react";
import {useHistory, useParams } from "react-router-dom"; //./이 없는경우는 라이브러리임
import styled from 'styled-components';
import "./detail.scss"

let box = styled.div`
padding:20px;
`;

let 제목 = styled.h4`
font-size : 25px;
color : ${ props => props.색상}
`;

function Detail(props) {
    let history = useHistory();
    let {id} = useParams();
    let [alert, changeAlert] = useState(true);

    useEffect( ()=> {
        let timer1 = setTimeout( ()=>{changeAlert(false)},2000);
        return function disappear() {
            // 사라질때 실행되는 함수
            clearTimeout(timer1);
        }
    },[alert]); //alert라는 이름의 state가 변경이 될 때만 업데이트로 치고 실행해줌
                      // []안에 아무것도 안넣는 경우, 그냥 컴포넌트 로드때만 한번 딱 실행하고 싶은 코드를 담을 때 쓰임

    let item = props.shoes.find(function(shoes) {
        return shoes.id == id;
    });
    return (
        <div className="container">
            {
                alert === true ? (
                    <div className="my-alert2">
                    <p>재고가 얼마 남지 않았습니다.</p>
                    </div>): null
            }

            <div className="row">
                <div className="col-md-6">
                    <img src={'https://codingapple1.github.io/shop/shoes' + (item.id+1) + '.jpg'} width="100%" />
                </div>
                <div className="col-md-6 mt-4">
                    <h4 className="pt-5">{item.title}</h4>
                    <p>{item.content}</p>
                    <p>{item.price}원</p>

                    <Info remainShoes = {props.remainShoes} ></Info>

                    <button className="btn btn-danger" onClick={ ()=> {props.updateRemainShoes([9,10,11])} }>주문하기</button>
                    &nbsp;
                    <button className="btn btn-danger" onClick={ ()=>{history.push('/')} }>뒤로가기</button>
                </div>
            </div>
        </div>
    )
}

function Info(props) {
    return (
        <p>재고 : {props.remainShoes[0]}개 </p>
    )
}
export default Detail;