import React, {useState} from 'react';
import './App.css';

import { Navbar,Nav,NavDropdown,Button,Jumbotron } from 'react-bootstrap';
import Data from './data.js';
import Detail from "./detail";
import axios from "axios";

import {Link, Route, Switch} from 'react-router-dom';

let remainShoesContext = React.createContext();

function App() {

  let [shoes, changeShoes] = useState(Data);
  let [remainShoes, updateRemainShoes] = useState([10,11,12]);

  return (
    <div className="App">
      <Navbar bg="light" expand="lg" className="">
        <Navbar.Brand href="#home">ShoeShop</Navbar.Brand>
        <Navbar.Toggle aria-controls="basic-navbar-nav" />
        <Navbar.Collapse id="basic-navbar-nav">
          <Nav className="ml-auto">
            <Nav.Link as={Link} to="/">Home</Nav.Link>
            <Nav.Link as={Link} to="/detail">Detail</Nav.Link>
            <NavDropdown title="Dropdown" id="basic-nav-dropdown">
              <NavDropdown.Item href="#action/3.1">Action</NavDropdown.Item>
              <NavDropdown.Item href="#action/3.2">Another action</NavDropdown.Item>
              <NavDropdown.Item href="#action/3.3">Something</NavDropdown.Item>
              <NavDropdown.Divider />
              <NavDropdown.Item href="#action/3.4">Separated link</NavDropdown.Item>
            </NavDropdown>
          </Nav>
        </Navbar.Collapse>
      </Navbar>

        <Switch>
        <Route exact path="/">
            <Jumbotron className="background">
                <h1>20% Season OFF</h1>
                <p>
                    This is a simple hero unit, a simple jumbotron-style component for calling
                    extra attention to featured content or information.
                </p>
                <p>
                    <Button variant="primary">Learn more</Button>
                </p>
            </Jumbotron>
            <div className="container">
                <remainShoesContext.Provider value={remainShoes}>
                <div className="row">
                    {
                        shoes.map( function(a , i){
                            return <Card shoe={a} i={i} key={i}></Card>
                        })
                    }
                </div>
                </remainShoesContext.Provider>
                <button className="btn btn-primary" onClick={ ()=>{
                    axios.get('https://codingapple1.github.io/shop/data2.json')
                        .then((result)=>{
                            console.log(result.data)
                            changeShoes([...shoes, ...result.data]);
                        })
                        .catch(()=>{ console.log("fail") });
                }}>더보기</button>
            </div>
        </Route>
            <Route path="/detail/:id">
                <Detail shoes={shoes} remainShoes = {remainShoes} updateRemainShoes = { updateRemainShoes }/>
            </Route>
        {/*<Route path="/modal" component={ Modal }></Route>*/}
        </Switch>
    </div>
  );
}

function Card(props) {
  return (
      <div className="col-md-4">
        <img src={'https://codingapple1.github.io/shop/shoes' + (props.i+1) + '.jpg'} width="100%"/>
        <h4 onClick={ ()=>{goDetail()}}>{props.shoe.title}</h4>
        <p>{props.shoe.content}, {props.shoe.price}원</p>
      </div>
  );
}

function goDetail() {

}

export default App;
